export class vec2 {
    constructor(x = 0, y = 0) {
        Object.defineProperty(this, "x", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: 0
        });
        Object.defineProperty(this, "y", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: 0
        });
        this.x = x;
        this.y = y;
    }
    sub(vec) {
        const { x: ax, y: ay } = this;
        const { x: bx, y: by } = vec;
        return new vec2(ax - bx, ay - by);
    }
    add(vec) {
        const { x: ax, y: ay } = this;
        const { x: bx, y: by } = vec;
        return new vec2(ax + bx, ay + by);
    }
    scale(scalar) {
        const { x, y } = this;
        return new vec2(x * scalar, y * scalar);
    }
    cross(vec) {
        const { x: ax, y: ay } = this;
        const { x: bx, y: by } = vec;
        return ax * by - ay * bx;
    }
    length() {
        const { x, y } = this;
        return Math.hypot(x, y);
    }
}
export default vec2;
