___CSS_LOADER_EXPORT___.push([module.id, `:root {
  font-family: "Inter", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  line-height: 1.5;
  font-weight: 400;
  color: #f8fafc;
  background-color: #0f172a;
}

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
}

body {
  overflow: hidden;
  background-color: #0f172a;
}

.app-shell {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #0f172a;
}

.canvas-container {
  flex: 1;
  position: relative;
  background-color: #0f172a;
}

canvas.graph-canvas {
  width: 100%;
  height: 100%;
  display: block;
  margin: 0;
}

.control-panel {
  width: 280px;
  height: 100%;
  background: #111d37;
  padding: 24px 20px;
  border-left: 1px solid rgba(148, 163, 184, 0.35);
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.control-panel__title {
  margin: 0;
  font-size: 1.2rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #93c5fd;
}

.control-panel__section {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.control-button {
  background: linear-gradient(90deg, #1e293b, #0f172a);
  border: 1px solid rgba(148, 163, 184, 0.5);
  color: #e2e8f0;
  padding: 10px 14px;
  font-size: 0.95rem;
  border-radius: 6px;
  text-align: left;
  cursor: pointer;
  transition: border-color 150ms ease, transform 150ms ease;
}

.control-button:hover {
  border-color: #38bdf8;
  transform: translateX(-2px);
}

.control-panel__label {
  font-size: 0.85rem;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #a5b4fc;
}

.json-textarea {
  width: 100%;
  min-height: 160px;
  background: #0b1324;
  border: 1px solid rgba(148, 163, 184, 0.4);
  border-radius: 6px;
  color: #e2e8f0;
  padding: 10px;
  font-family: "JetBrains Mono", "SFMono-Regular", Consolas, "Liberation Mono", monospace;
  font-size: 0.85rem;
  resize: vertical;
}

.json-textarea:focus {
  outline: none;
  border-color: #38bdf8;
  box-shadow: 0 0 0 1px rgba(56, 189, 248, 0.3);
}

.control-panel__error {
  margin: 0;
  font-size: 0.8rem;
  color: #f87171;
}
